.swipeButtons{
    display: flex;
    width: 100%;
    position: fixed;
    bottom: 8vh;
    justify-content: space-evenly;
}

.swipeButtons .MuiIconButton-root{
    background-color: white;
    box-shadow: 0 10px 53px 0px rgba(0, 0, 0, 0.3) !important;
}

.SwipeButtons-repeat{
    padding: 3vw;
    color: #f5b748 !important;   
}

.SwipeButtons-left{
    padding: 3vw;
    color: #ec5e6f !important;   
}

.SwipeButtons-star{
    padding: 3vw;
    color: #62b4f9 !important;   
}

.SwipeButtons-right{
    padding: 3vw;
    color: #76e2b3 !important;   
}

.SwipeButtons-Lightning{
    padding: 3vw;
    color: #91fdd1 !important;   
}

@media (max-width:770px){
    .swipeButtons{
        bottom: 5vh;
    }
}