h1,h2,h3{
    margin: 0;
}

.card{
    width: 600px;
    max-width: 85vw;
    height: 50vh;
    position: relative;
    padding: 20px;
    background-color: #fff;
    background-size: cover;
    box-shadow: 0 18px 53px 0 rgba(0, 0,0,0.3);
    border-radius: 20px;
    background-position: center;
}

.tinderCards-cardsContainer{
    display: flex;
    justify-content: center;
    margin-top: 10vh;
}

.onSwipe{
    position: absolute;
}

.card h3{
    position: absolute;
    bottom: 0;
    margin: 10px;
    color:#fff;
}